'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
require('swiper');
var template = "<div class=\"swiper-container\">\n  <div class=\"swiper-wrapper\">\n    <% for (var i = 0; i < items.length; i++) { %>\n        <div class=\"swiper-slide\" style=\"background: linear-gradient(to right, rgba(0, 0, 0, 0.5),  rgba(0, 0, 0, 0.5)), url('/static/files/<%= items[i].image %>') no-repeat center center; -webkit-background-size: cover;  -moz-background-size: cover; -o-background-size: cover; background-size: cover;\">\n          <div class=\"c-featured-item\">\n            <div class=\"c-featured-item__background\"></div>\n            <article class=\"c-featured-item__container\">\n              <h1 class=\"c-featured-item__container__title\"><%= items[i].show %></h1>\n              <p class=\"c-featured-item__container__tagline\"><%= items[i].tagline %></p>\n              <p class=\"c-featured-item__container__tagline\"><a href=\"#shows/<%= items[i].show_slug %>\">SEE ALL EPISODES</a></p>\n            </article>\n          </div>\n        </div>\n    <% } %>\n  </div>\n  <div class=\"swiper-pagination\"></div>\n</div>\n";

module.exports = Backbone.View.extend({
  className: 'o-feature-slider',
  showcaseItems: [],

  render: function render() {
    this.$el.html(_.template(template)({
      items: this.showcaseItems,
    }));
    return this;
  },

  initSwiper: function() {
    // Needs to be triggered after Virtual DOM is attached.
    if (this.$('.swiper-container').length) {
      this.swiper = new Swiper('.swiper-container', {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          preventClicks: false,
          keyboardControl: true,
          autoplay: 5000,
      });
    }
  },

  getShowcaseItems: function getShowcaseItems() {
    // Return maximum of 5 episodes to showcase on the main page.
    $.getJSON(window.app.apiURL + "/api/new-episodes")
      .done((data) => {
        this.showcaseItems = data.items;
        this.render();
        this.initSwiper();
      });
  }
});