'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
var template = "<section class=\"c-content\">\n  <div class=\"row\">\n    <div class=\"col\">\n      <h1>Wiki</h1>\n      <ul>\n        <li><a href=\"#wiki/artist\">Artists</a></li>\n        <li><a href=\"#wiki/city\">Cities</a></li>\n        <li><a href=\"#wiki/country\">Countries</a></li>\n      </ul>\n    </div>\n  </div>\n</section>";

module.exports = Backbone.View.extend({
  className: 'o-content-block',

  render: function render() {
    this.$el.html(_.template(template));
    return this;
  }
});