'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
var template = "<section class=\"c-content\">\n  <div class=\"row\">\n    <div class=\"col\">\n      <h1><a class='u-no-border' href=\"#wiki\">Wiki</a> &rsaquo; <%= typePretty %></h1>\n      <% if (data !== undefined){ %>\n        <ul>\n          <% for(var i = 0; i < data.tags.length; i++) { %>\n            <li><a href='#wiki/<%= data.tags[i].link %>'><%= data.tags[i].name %></a></li>\n          <% } %>\n      <% } %>\n    </div>\n  </div>\n</section>";

module.exports = Backbone.View.extend({
  className: 'o-content-block',

  initialize: function(options) {
    this.tagType = options.type;
    if (this.tagType === 'artist') {
      this.tagTypePretty = "Artists";
    } else if (this.tagType === 'city') {
      this.tagTypePretty = "Cities";
    } else {
      this.tagTypePretty = "Countries";
    }
    $.getJSON(window.app.apiURL + "/api/tags/" + this.tagType)
      .done((data) => {
        console.log(data);
        this.tagData = data;
        this.render();
        document.title = this.tagTypePretty + " | Frequency Asia";
      });
  },

  render: function render() {
    this.$el.html(_.template(template)({
      'type': this.tagType,
      'typePretty': this.tagTypePretty,
      'data': this.tagData,
    }));
    return this;
  }
});