'use strict';

var Backbone = require('backbone');
var _ = require("lodash");
var $ = require("jquery");

var template = "<% for (var i = 0; i < data.length; i++) { %>\n    <a href=\"#shows/<%= data[i].slug %>\" class='post-module'>\n      <div class='thumbnail'>\n        <img src='/static/files/<%= data[i].imagePath %>' alt='<%= data[i].name %>'>\n      </div>\n      <div class='post-content'>\n        <h1 class='title'><%= data[i].name %></h1>\n        <h2 class='sub_title'><%= data[i].frequency %></h2>\n        <p class='description'><%= data[i].tagline %></p>\n        <div class='post-meta'>\n          <span class='timestamp'>\n            <% if (data[i].num_episodes == 1) { %>\n              1 Episode\n            <% } else { %>\n              <%= data[i].num_episodes %> Episodes\n            <% } %>\n          </span>\n        </div>\n      </div>\n    </a>\n<% } %>";

module.exports = Backbone.View.extend({

  tagName: 'section',
  className: 'o-content-block u-full-width-mobile',
  shows: [],

  initialize: function() {
    var _this = this;
    $.getJSON(window.app.apiURL + "/api/shows/")
      .done(function(data) {
        _this.shows = data.shows;
        _this.render();
      });
  },

  render: function render() {
    this.$el.html(_.template(template)({"data": this.shows}));
    this.$('.post-module').hover(function() {
      $(this).find('.description').stop().animate({height: "toggle", opacity: "toggle"}, 300);
    });
    return this;
  },
});