'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
var fecha = require("fecha");
var template = "<section class=\"c-content\">\n  <div class=\"row\">\n    <div class=\"col\">\n      <% if (data !== undefined){ %>\n        <h1><a class='u-no-border' href=\"#wiki\">Wiki</a> &rsaquo; <a class='u-no-border' href=\"#wiki/<%= type %>\"><%= typePretty %></a> &rsaquo; <%= data.name %></h1>\n        <% for(var i = 0; i < data.episodes.length; i++) { %>\n          <article class=\"c-episode\">\n            <div class=\"c-episode__content\">\n              <div class=\"c-episode__content__play js-play-episode\" data-mixcloud=\"<%= data.episodes[i].mixcloud_link %>\">\n                <span class=\"icon-play2\"></span>\n              </div>\n              <div class=\"c-episode__content__info\">\n                <h1 class=\"c-episode__content__title\"><a href=\"#shows/<%= data.episodes[i].show_slug %>\"><%= data.episodes[i].name %></a></h1>\n                <p><%= data.episodes[i].tagline %></p>\n                <p class=\"c-episode__content__date\"><%= data.episodes[i].date %></p>\n                <p class=\"c-episode__content__tags\">\n                  <% var tags = data.episodes[i].tags.countries.concat( data.episodes[i].tags.cities).concat( data.episodes[i].tags.artists); %>\n                  <% for (var j = 0; j < tags.length; j++) { %>\n                    <a href=\"#wiki/<%= tags[j].link %>\"><%= tags[j].name %></a>\n                    <% if(j != tags.length - 1){ %>\n                      &nbsp;/&nbsp;\n                    <% } %>\n                  <% } %>\n                </p>\n              </div>\n              <% if(data.episodes[i].episode_image) { %>\n                <img class=\"c-episode__content__image\" src='/static/files/<%= data.episodes[i].episode_image %>' alt='<%= data.episodes[i].name %>'/>\n              <% } %>\n            </div>\n            <div class=\"c-episode__description-toggle\">+ More Info</div>\n            <div class=\"c-episode__description\"><%= data.episodes[i].description %></div>\n          </article>\n        <% } %>\n      <% } %>\n    </div>\n  </div>\n</section>";

module.exports = Backbone.View.extend({
  className: 'o-content-block',

  initialize: function(options) {
    this.tagType = options.type;
    if (this.tagType === 'artist') {
      this.tagTypePretty = "Artists";
    } else if (this.tagType === 'city') {
      this.tagTypePretty = "Cities";
    } else {
      this.tagTypePretty = "Countries";
    }
    $.getJSON(window.app.apiURL + "/api/tags/" + this.tagType + "/" + options.slug)
      .done((data) => {
        for (var item of data.episodes) {
          item.date = fecha.format(new Date(item.start_time), 'dddd / MMMM D YYYY');
        }
        this.tagData = data;
        this.render();
        document.title = data.name + " | " + this.tagTypePretty + " | Frequency Asia";
      });
  },

  render: function render() {
    this.$el.html(_.template(template)({
      'type': this.tagType,
      'typePretty': this.tagTypePretty,
      'data': this.tagData,
    }));
    this.$('.c-episode__description-toggle').click((event) => {
      var $el = $(event.currentTarget)
      if ($el.next().hasClass("c-episode__description--toggled")) {
        $el.next().removeClass("c-episode__description--toggled");
        $el.text('+ More Info');
      } else {
        $el.next().addClass("c-episode__description--toggled");
        $el.text('- Less Info');
      }
    });
    return this;
  }
});