'use strict';

var Backbone = require('backbone');
var _ = require("lodash");
var $ = require("jquery");

var fecha = require("fecha");
var template = "<aside class=\"c-show__sidebar\">\n  <% if(data !== undefined) { %>\n    <img class=\"c-show__sidebar__image\" src='/static/files/<%= data.imagePath %>' alt='<%= data.name %>'>\n    <h1 class=\"c-show__sidebar__title\"><%= data.name %></h1>\n    <p><%= data.description %></p>\n  <% } %>\n</aside>\n<section class=\"c-show__episodes\">\n  <% if(data !== undefined) { %>\n    <% for(var i = 0; i < data.episodes.length; i++) { %>\n      <article class=\"c-episode\">\n        <div class=\"c-episode__content\">\n          <div class=\"c-episode__content__play js-play-episode\" data-mixcloud=\"<%= data.episodes[i].mixcloud_link %>\">\n            <span class=\"icon-play2\"></span>\n          </div>\n          <div class=\"c-episode__content__info\">\n            <h1 class=\"c-episode__content__title\"><%= data.episodes[i].name %></h1>\n            <p><%= data.episodes[i].tagline %></p>\n            <p class=\"c-episode__content__date\"><%= data.episodes[i].date %></p>\n            <p class=\"c-episode__content__tags\">\n              <% var tags = data.episodes[i].tags.countries.concat( data.episodes[i].tags.cities).concat( data.episodes[i].tags.artists); %>\n              <% for (var j = 0; j < tags.length; j++) { %>\n                <a href=\"#wiki/<%= tags[j].link %>\"><%= tags[j].name %></a>\n                <% if(j != tags.length - 1){ %>\n                  &nbsp;/&nbsp;\n                <% } %>\n              <% } %>\n            </p>\n          </div>\n          <% if(data.episodes[i].episode_image) { %>\n            <img class=\"c-episode__content__image\" src='/static/files/<%= data.episodes[i].episode_image %>' alt='<%= data.episodes[i].name %>'/>\n          <% } %>\n        </div>\n        <div class=\"c-episode__description-toggle\">+ More Info</div>\n        <div class=\"c-episode__description\"><%= data.episodes[i].description %></div>\n      </article>\n    <% } %>\n  <% } %>\n</section>";

module.exports = Backbone.View.extend({
  className: 'c-show',
  tagName: 'section',

  events: {
    'click .js-play-episode': 'onPlayEpisodeClicked',
  },

  initialize: function(options) {
    var _this = this;
    $.getJSON(window.app.apiURL + "/api/shows/" + options.slug)
      .done((data) => {
        for (var i = 0; i < data.episodes.length; i++) {
          var item = data.episodes[i];
          item.date = fecha.format(new Date(item.start_time), 'dddd / MMMM D YYYY');
        }
        this.showData = data;
        this.render();
        document.title = this.showData.name + " | Frequency Asia";
      });
  },

  render: function render() {
    this.$el.html(_.template(template)({"data": this.showData}));
    this.$('.c-episode__description-toggle').click((event) => {
      var $el = $(event.currentTarget)
      if ($el.next().hasClass("c-episode__description--toggled")) {
        $el.next().removeClass("c-episode__description--toggled");
        $el.text('+ More Info');
      } else {
        $el.next().addClass("c-episode__description--toggled");
        $el.text('- Less Info');
      }
    });
    return this;
  },

  onPlayEpisodeClicked: function onPlayEpisodeClicked(event) {
    var url = this.$(event.currentTarget).data("mixcloud");
    window.app.views.playerView.setMixcloudURL(url);
  },
});