'use strict';

var Backbone = require('backbone');
var _ = require("lodash");

var $ = require("jquery");
var template = "<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#\">home</a>\n</div>\n<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#shows\">shows</a>\n</div>\n<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#schedule\">schedule</a>\n</div>\n<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#wiki\">wiki</a>\n</div>\n<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#projects\">projects</a>\n</div>\n<div class=\"c-nav__item js-nav-item\">\n  <a href=\"#about\">about</a>\n</div>";

module.exports = Backbone.View.extend({
  render: function render() {
    this.$el.html(_.template(template));
    return this;
  },

  setActivePage: function setActivePage() {
    $(".js-nav-item").removeClass("c-nav__item--active").addClass("c-nav__item")
    var subURL = window.location.href.split("#")[1];
    if (subURL === undefined) {
        $('.js-nav-item').first().removeClass('c-nav__item').addClass('c-nav__item--active');
    } else {
      var url = "#" + subURL;
      var $navItem = $('.js-nav-item a[href="' + url + '"]');
      if ($navItem.length) {
        $navItem.parent().removeClass('c-nav__item').addClass('c-nav__item--active');
      }
    }
  },
});